import { MDB_TYPES } from "@meddbriefer/scenario-data/constants";
import { useEffect } from "react";
const MDB = MDB_TYPES;

const Hierarchy = ({ data, upload, hierarchyState }) => {
    const headings = data.filter(e => e.type === MDB.PHASE || e.type === MDB.SUB_PHASE);
    const [hierarchy, setHierarchy] = hierarchyState;

    useEffect(() => {
        // Compute updated hierarchy since last meta-data update
        let hierarchy = {};

        for (let item of data)
        {
            if (item.type === MDB.PHASE || item.type === MDB.SUB_PHASE) continue;
            if (!hierarchy[item.relation] && item.relation) 
                hierarchy[item.relation] = [];

            item.relation && 
                hierarchy[item.relation].push(item);
        }

        setHierarchy(hierarchy);
    }, [data, setHierarchy]);

    const swapUp = (e) => {
        const item = e.target.parentNode;
        const phase = item.parentNode.id;
        const idx = item.getAttribute("idx");
        if (idx > 0) {
            const temp = hierarchy[phase][idx - 1];
            hierarchy[phase][idx - 1] = hierarchy[phase][idx];
            hierarchy[phase][idx] = temp;
            setHierarchy({...hierarchy});
        }
    }
    
    const swapDown = (e) => {
        const item = e.target.parentNode;
        const phase = item.parentNode.id;
        const idx = item.getAttribute("idx");
        if (idx < hierarchy[phase].length - 1) {
            const temp = hierarchy[phase][parseInt(idx) + 1];
            hierarchy[phase][parseInt(idx) + 1] = hierarchy[phase][idx];
            hierarchy[phase][idx] = temp;
            setHierarchy({...hierarchy});
        }
    }
        
    return (
        <div className="meta-data-hierarchy">
            {headings.map(phase => hierarchy[phase.id] && (
                <div id={phase.id} key={phase.id} className="hierarchy-block">
                    <h3>{phase.label}</h3>
                    {hierarchy[phase.id].map((item, i) => (
                        <div idx={i} key={item.id} className="hierarchy-item">
                            <button type="button" onClick={swapUp}></button>
                            <button type="button" onClick={swapDown}></button>
                            <p>{item.label}</p>
                        </div>
                    ))}
                </div>
            ))}
            <button type="button" onClick={upload}>Submit</button>
        </div>
    );
}

export default Hierarchy;