
import Vitals from "./Vitals";
import SAMPLE from "./Sample";
import OPQRST from "./OPQRST";

const PatientStatus = ({active, scenario}) => {

    return (
        <>
        <Vitals active={active} scenario={scenario} />
        <div style={{ display: "flex" }}>
            <div style={{flex: 1}}>
                <SAMPLE active={active} scenario={scenario} />
            </div>
            <div style={{ flex: 1 }}>
                <OPQRST active={active} scenario={scenario} />
            </div>
        </div>

        </>
    )
}

export default PatientStatus