import { useEffect, useRef, useState } from 'react';

import { getDocumentWithName, saveDocument } from '../../scripts/connect-firebase.js';
// import { CHECKLIST_META_DATA } from '@meddbriefer/scenario-data/traumaScenarioV2MetaData';
import { MDB_TYPES as MDB } from '@meddbriefer/scenario-data/constants.js';
import MetaItem from './MetaItem';
import Hierarchy from './Hierarchy.js';

const COLLECTION_ID = "scenario-types";
const DOCUMENT_NAME = "trauma-checklist";

const MetaDataEditor = () => {
    const [data, setData] = useState();
    const [view, setView] = useState(MDB.PHASE);
    const [phaseTypes, setPhaseTypes] = useState({ subPhase: [], checkItem: [] });
    const [hierarchy, setHierarchy] = useState({});
    const idRef = useRef();
    const formRef = useRef();

    useEffect(() => {
        getDocumentWithName(COLLECTION_ID, DOCUMENT_NAME)
            .then(metadata => {
                idRef.current = metadata.id;
                const newData = metadata.checklist;
                setData(newData);
                setHierarchy(metadata.hierarchy);

                // Update phase type exclusions
                let types = { subPhase: [], checkItem: [] };
                newData.forEach((item, i) => {
                    if (item.type === MDB.SUB_PHASE)
                        types.subPhase[i] = item.relation;
                    else if (item.type !== MDB.PHASE)
                        types.checkItem[i] = item.relation;
                });
                setPhaseTypes(types);
            })
            .catch(console.error);
    }, []);

    const isCurrentTab = (id) => view === id ? "selected" : "";
    const isOption = (e) => e.type === MDB.ASSESS_OPTION || e.type === MDB.DECISION_OPTION;

    return (
        <form className="checklist-meta-data" autoComplete="off" ref={formRef} >
            <div className="tab-selector">
                <span className={`tab ${isCurrentTab(MDB.PHASE)}`} 
                    onClick={() => setView(MDB.PHASE)}>Phases</span>

                <span className={`tab ${isCurrentTab(MDB.SUB_PHASE)}`} 
                    onClick={() => setView(MDB.SUB_PHASE)}>Sub-Phases</span>

                <span className={`tab ${isCurrentTab(MDB.ASSESS)}`} 
                    onClick={() => setView(MDB.ASSESS)}>Assessments</span>

                <span className={`tab ${isCurrentTab(MDB.AWO)}`} 
                    onClick={() => setView(MDB.AWO)}>Assessments With Options</span>

                <span className={`tab ${isCurrentTab(MDB.REQURED)}`} 
                    onClick={() => setView(MDB.REQURED)}>Required</span>

                <span className={`tab ${isCurrentTab(MDB.DECISION)}`} 
                    onClick={() => setView(MDB.DECISION)}>Decisions</span>

                <span className={`tab ${isCurrentTab('hierarchy')}`} 
                    onClick={() => setView('hierarchy')}>Hierarchy</span>
            </div>

            {data ? data.map((e, i) => (
                !isOption(e) &&
                    <MetaItem 
                        key={e.id} 
                        data={data}
                        item={e} 
                        display={e.type === view} 
                        index={i} 
                        updateData={setData}
                        phaseState={[phaseTypes, setPhaseTypes]}
                        />
            )) : <p>Loading...</p> }

            {(view === 'hierarchy') && 
                <Hierarchy data={data} upload={upload} hierarchyState={[hierarchy, setHierarchy]} />}
        </form>
    );

    async function upload() {
        // let dt = data.filter(e => e.type === MDB.PHASE || e.type === MDB.SUB_PHASE);
        // let phases = dt.filter(e => e.type === MDB.PHASE);

        // let optionItems = data.filter(e => e.type === MDB.AWO || e.type === DECISION);
        // let options = data.filter(e => e.type === ASSESS_OPTION || e.type === MDB.DECISION_OPTION);
        
        // // Build options with prompts
        // for (let option of options)
        // {
        //     const optionItem = optionItems.find(e => e.id === option.promptID);

        //     if (optionItem)
        //         optionItem.options
        //             ? optionItem.options.push(option)
        //             : optionItem.options = [option];
        // }

        // // Build subphases and phases
        // for (let item of dt)
        // {
        //     // Phase with hierarchical checklist items
        //     if (item.id in hierarchy) 
        //         item.items = hierarchy[item.id];

        //     // Phases with subphases
        //     const phaseItem = phases.find(e => e.id === item.relation);

        //     if (phaseItem)
        //         phaseItem.subPhases 
        //             ? phaseItem.subPhases.push(item)
        //             : phaseItem.subPhases = [item];
        // }

        // const metaData = { id: idRef.current, name: DOCUMENT_NAME, checklist: phases };
        const metaData = { 
            id: idRef.current, 
            name: DOCUMENT_NAME, 
            checklist: data, 
            hierarchy: hierarchy 
        };

        (window.confirm("Upload changes?")) 
            && await saveDocument(COLLECTION_ID, metaData);

        return metaData;
    }
}

