import React from "react"
import { Link } from "react-router-dom"

const HomePage = () => {
	return (
		<nav className="home-nav-wrapper">
			<Link to="/scenarios" className="home-nav-link">
				Scenario Forms
			</Link>
			<Link to="/class-codes-list/" className="home-nav-link">
				Class Codes
			</Link>
			<Link to="/debriefer-entry-list/" className="home-nav-link">
				Critical Actions
			</Link>
			<Link to="/checklist-meta-editor" className="home-nav-link">
				Checklist Meta Data
			</Link>
			<Link to="/medications-form" className="home-nav-link">
				Medications
			</Link>
		</nav>
	);
}

export default HomePage