import classnames from 'classnames';

import { MDB_TYPES as TYPES } from "@meddbriefer/scenario-data/constants.js"
import { CHECKLIST_META_DATA } from "@meddbriefer/scenario-data/traumaScenarioV2MetaData.js";

import ScenarioItem from "./ScenarioItem.js";

// filter out checklist items, other than PHASEs and SUB_PHASEs (for headings) which don't have findings
const scenarioData = CHECKLIST_META_DATA
	.filter(e => ![TYPES.AWO, TYPES.CL_PROMPT, TYPES.DECISION, TYPES.DECISION_OPTION, TYPES.REQURED].includes(e.type))
	// .filter(e => !e.id.includes("critical") && e.type !== CL_PROMPT && e.type !== REQURED);

const Steplist = ({ active, scenario }) => {
	return (
		<div id="scenario-steps" className={classnames({"scenario-view-hidden": !active})}>
			<h1>Scenario Items</h1>
			{scenarioData.map((item) => (
				<ScenarioItem key={item.id} item={item} scenario={scenario} />
			))}
		</div>
	);
};

export default Steplist;
