import { useState, useEffect, useRef } from "react";

// import { MEDS } from '@meddbriefer/scenario-data/traumaScenarioV2MetaData.js';
import { getDocumentWithName, saveDocument } from "../../scripts/connect-firebase";
import Medication from "./Medication";

const COLLECTION_ID = "meta-data";
const DOCUMENT_NAME = "medications";

export default function Medications() {
    const [meds, setMeds] = useState([]);
    const idRef = useRef()
    const formRef = useRef();

    useEffect(() => {
        getDocumentWithName(COLLECTION_ID, DOCUMENT_NAME)
            .then(doc => {
                idRef.current = doc.id;
                setMeds(doc.medications)
            })
            .catch(console.error);
    }, []);

    function addMed() {
        setMeds([...meds, {}]);
    }

    function submit() {
        const doc = { 
            id: idRef.current, 
            name: DOCUMENT_NAME, 
            medications: meds 
        };

        (window.confirm("Upload medications?"))
            && saveDocument(COLLECTION_ID, doc);
    }

    return (
        <form className="medications-form" ref={formRef}>
            {meds.map((med, i) => (
                <Medication key={med.id} med={med} index={i} medState={[meds, setMeds]} />
            ))}
            
            <div className="button-wrapper">
                <button type="button" onClick={addMed}>New</button>
                <button type="button" onClick={submit}>Submit</button>
            </div>
        </form>
    );
}