import React, { useState, useEffect, useRef } from "react";
import { useHistory } from "react-router-dom";
import classnames from 'classnames';
// import { isCompositeComponent } from "react-dom/test-utils";

import {
	getDocument,
	saveDocument,
	verifyDocument,
} from "../../scripts/connect-firebase.js";

import blankTrauma from '../../data/blank-trauma.json';
import { updateSchema } from "./traumaScenario.js";

// import TabButton from "./TabButton.js";

import BasicInfo from "./BasicInfo.js";
import PatientStatus from "./PatientStatus.js";
import Steplist from "./Steplist.js";
import CheckListCorrectness from "./CheckListCorrectness.js";
import Medications from "./Medications.js";
import CriticalActions from "./CriticalActions.js";
import InterventionAnswers from "./InterventionAnswers.js";

const COLLECTION_ID = 'scenarios';

const ScenarioForm = (props) => {
	const { scenarioId } = props.match.params;
	const { asTemplate } = props.match.params;
	const [scenario, setScenario] = useState();
	const [activeTab, setActiveTab] = useState('basic-info');
	const isActiveTab = (name) => name === activeTab;
	const isEdit = !!scenarioId;
	const formEl = useRef(null);
	let history = useHistory();

	// Load and apply selected scenario
	useEffect(() => {
		isEdit
			? getDocument(COLLECTION_ID, scenarioId)
				.then(doc => {
					// update db record adding in new fields and throw away ones no longer used
					setScenario(updateSchema(doc, blankTrauma))
				})
				.catch(error => console.log(error))
			: setScenario(updateSchema({}, blankTrauma));
	}, [scenarioId, isEdit]);

	useEffect(() => {
		window.scenario = scenario
	}, [scenario]);

	return (
		<div className="scenario-form-wrapper">
			<nav id="tab-selector">
				<div
					className={classnames("tab-button", {"active-tab": isActiveTab("basic-info")})}
					onClick={() => setActiveTab('basic-info')}
				>
					Info
				</div>
			    <div
					className={classnames("tab-button", {"active-tab": isActiveTab("patient-status")})}
					onClick={() => setActiveTab('patient-status')}
				>
					Patient Status
				</div>

				<div
					className={classnames("tab-button", { "active-tab": isActiveTab("steplist") })}
					onClick={() => setActiveTab('steplist')}
				>
					Assessment Findings
				</div>
				<div
					className={classnames("tab-button", { "active-tab": isActiveTab("checklist-correctness") })}
					onClick={() => setActiveTab('checklist-correctness')}
				>
					Checklist Correctness
				</div>

				<div
					className={classnames("tab-button", {"active-tab": isActiveTab("medications")})}
					onClick={() => setActiveTab('medications')}
				>
					Medications
				</div>
				<div
					className={classnames("tab-button", {"active-tab": isActiveTab("critical-actions")})}
					onClick={() => setActiveTab('critical-actions')}
				>
					Critical Actions
				</div>
				<div
					className={classnames("tab-button", {"active-tab": isActiveTab("intv-answers")})}
					onClick={() => setActiveTab("intv-answers")}
				>
					Intervention Answers
				</div>
				<button type="submit" id="submit-next" onClick={() => submitForm()}>Submit</button>
			</nav>

			<form id="scenario-form" className="scenario-form" ref={formEl}>
				{
				scenario ?
				<>
					<BasicInfo 		      active={isActiveTab('basic-info')} 	        scenario={scenario} edit={isEdit} />
					<PatientStatus        active={isActiveTab('patient-status')}        scenario={scenario} />
					<Medications 	      active={isActiveTab('medications')}           scenario={scenario} />
					<Steplist 		      active={isActiveTab('steplist')}	            scenario={scenario} />
					<CheckListCorrectness active={isActiveTab('checklist-correctness')} scenario={scenario} />
					<CriticalActions 	  active={isActiveTab('critical-actions')}      scenario={scenario} />
					<InterventionAnswers  active={isActiveTab("intv-answers")}          scenario={scenario} />
				</>
				: <p>Loading...</p> // While scenario is loading in
				}
			</form>
		</div>
	);

	async function submitForm() {
		let form = formEl.current;

		if (asTemplate === 'template' && form.name.value === scenario.name)
		{
			alert('You can\'t use the same name as the template!');
		}
		else if ( form.name.value == null || form.name.value.trim() === "" || ( !await verifyDocument(COLLECTION_ID, form.name.value) && !isEdit ) )
		{
			// If scenario exists and not in edit mode
			alert('Scenario needs a new name!');
		}
		else if (window.confirm("Any empty items will be filled with default information. Submit?"))
		{
			form = processForm(form);
			// console.log("form.id", form.id);

			await saveDocument(COLLECTION_ID, form)
			history.push("/scenarios"); // Send back once submitted
		}
		else
		{
			console.log("Submit cancelled.");
		}
	}

	function processForm(data) {
		let input, id, path,
		newScenario = deepCopy(scenario);

		for(let item of data) {
			id = item.id;
			path = id.split('-');
			input = item.value;

			// Default inputs
			if (!input) {
				switch (path[0]) {
					case 'assessmentFindings':
						input = "Unremarkable";
						break;
					case 'OPQRST':
						input = "Unable to assess";
						break;
					case 'correctMedicationDosages':
						input = "default";
						break;

					default: break;
				}
			}

			if (input) { // Don't process empty fields
				// Reconstruct assessmentFindings path after split
				if (path[0] === 'assessmentFindings')
					path = [path[0], id.split('assessmentFindings-')[1]];

				// Read numbers as numbers
				input = isNaN(input) ? input : parseInt(input);

				// Handle different object depths
				switch (path.length) {
					case 1: newScenario[path[0]] = input; break;
					case 2: newScenario[path[0]][path[1]] = input; break;
					case 3: newScenario[path[0]][path[1]][path[2]] = input; break;

					default: console.error('Path length error:', path.length); break;
				}
			}
		}

		if (isEdit && !asTemplate) {
			newScenario.id = scenarioId
		}

		return newScenario;
	}
};

function deepCopy(obj) {
	return JSON.parse(JSON.stringify(obj));
}

export default ScenarioForm;
