const expandPromptSetToPromptList = ({id, constraints = null, dataEntry = true}) => {
    const prompts = PROMPTS.filter(rec => rec.promptSetID === id)
    return prompts.map(prompt => {
        const data = {
            id: prompt.id,
            dataEntry: dataEntry
        }
        if (!!constraints) {
            data.showConstraints = constraints
        }
        return data
    })
}

const getSystemIntvs = (system) => {
    return INTV_META_DATA.filter(rec => rec.type === TYPES.INTERV && rec.system === system)
}

const getIntervention = (intvID, depth) => {
    const obj = Object.assign({}, getIntvData(intvID), { depth: depth })
    if (!!obj.system) {
        delete obj.system
    }
    return obj
}


//
// functions for generating derived data
//

const createInterventionForm = ({ intv, prompts = [] }) => {
    const data = Object.assign({}, getIntvData(intv))
    data.prompts = []
    // pass this intervention down so that it can be associatied with the options so we
    // know the context of what intervention was being performed when that answe was selected
        // prompts.forEach(prompt => data.prompts.push(...getPromptSet(prompt)))
    prompts.forEach(prompt => data.prompts.push(...expandPromptSetToPromptList(prompt)))
    INTV_FORMS[intv] = data
}



const mkPhase = ({ phaseType, phase, id, items = [], subPhases = [], interventionsMenu = [], dataEntry = true }) => {
    const headingID = (phaseType === TYPES.PHASE) ? `${PFX.PHASE}${id}` : `${PFX.SUB_PHASE}${id}`
    let obj = getChecklistData(headingID)
    obj.id = id
    obj.type = phaseType
    obj.dataEntry = dataEntry
    if (subPhases.length && items.length) {
        throw Error(`a ${phaseType} cannot have both subPhases AND checklist items`)
    }
    if (subPhases.length) {
        obj.subPhases = subPhases.map(sp => {
            let cld = getChecklistData(`${PFX.SUB_PHASE}${sp}`)
            cld.id = sp
            return cld
        })
    } else {
        obj.items = items.map(it => {
            let cld = Object.assign({}, getChecklistData(it), { phase: phase })
            return cld
        })
    }
    return obj
}

const createPhase = ({ id, items = [], subPhases = [], interventionsMenu = [], dataEntry = true }) => {
    return mkPhase({ phaseType: TYPES.PHASE, phase: id, id, items, subPhases, interventionsMenu, dataEntry })
}

const createSubPhase = ({ id, phase, items = [], subPhases = [], interventionsMenu = [], dataEntry = true }) => {
    return mkPhase({ phaseType: TYPES.SUB_PHASE, phase, id, items, subPhases, interventionsMenu, dataEntry })
}


