//
// meta-data/derived-data sanity checking routines
//

const checkForDupIDs = () => {
    let errors = []
    let IDs = {}
    CHECKLIST_META_DATA.forEach((rec) => {
        if (!(rec.id in IDs)) {
            IDs[rec.id] = 1
        } else {
            IDs[rec.id]++
        }
    })
    INTV_META_DATA.forEach((rec) => {
        if (!(rec.id in IDs)) {
            IDs[rec.id] = 1
        } else {
            IDs[rec.id]++
        }
    })
    const dupIDs = Array.from(Object.entries(IDs)).filter(([id, count]) => count > 1)
    if (dupIDs.length > 0) {
        errors.push("Duplicate IDs found!")
        // console.error()
        errors.push(JSON.stringify(dupIDs, null, 4))
    }
    return errors
}

const enforceNamingConventions = () => {
    let errors = []
    // const badPhases = getNamingConventionViolations(PHASES, PFX.PHASE)
    // const badSubPhases = getNamingConventionViolations(SUBPHASES, PFX.SUB_PHASE)
    const badAssessments = getNamingConventionViolations(ASSESSMENTS, PFX.ASSESS)
    const badAwos = getNamingConventionViolations(ASSESS_WITH_OPTIONS, PFX.AWO)
    const badAssessOptions = getNamingConventionViolations(ASSESS_OPTIONS, PFX.ASSESS_OPTION)
    const badRequiredActions = getNamingConventionViolations(REQ_ACTIONS, PFX.REQURED)
    const badDecisions = getNamingConventionViolations(DECISIONS, PFX.DECISION)
    const badDecisionOptions = getNamingConventionViolations(DECISION_OPTIONS, PFX.DECISION_OPTION)
    const badCheckListPrompts = getNamingConventionViolations(CL_PROMPTS, PFX.CL_PROMPT)
    const badIntvs = getNamingConventionViolations(INTVS, PFX.INTERV)
    const badPromptSets = getNamingConventionViolations(PROMPT_SETS, PFX.PROMPT_SET)
    const badPrompts = getNamingConventionViolations(PROMPTS, PFX.PROMPT)
    const badAnswers = getNamingConventionViolations(ANSWERS, PFX.ANSWER)

    if (anyHaveValues(
        // badPhases,
        // badSubPhases,
        badAssessments,
        badAwos,
        badAssessOptions,
        badRequiredActions,
        badDecisions,
        badDecisionOptions,
        badPromptSets,
        badCheckListPrompts,
        badPrompts,
        badIntvs,
        badAnswers)) {
        // errors.concat(reportViolations("phases",                   PFX.PHASE,           badPhases))
        // errors.concat(reportViolations("sub phases",               PFX.SUB_PHASE,       badSubPhases))
        errors.concat(reportViolations("assessments",              PFX.ASSESS,          badAssessments))
        errors.concat(reportViolations("assessments with options", PFX.AWO,             badAwos))
        errors.concat(reportViolations("assement options",         PFX.ASSESS_OPTION,   badAssessOptions))
        errors.concat(reportViolations("required actions",         PFX.REQURED,         badRequiredActions))
        errors.concat(reportViolations("decisions",                PFX.DECISION,        badDecisions))
        errors.concat(reportViolations("decision options",         PFX.DECISION_OPTION, badDecisionOptions))
        errors.concat(reportViolations("checklist prompts",        PFX.CL_PROMPT,       badCheckListPrompts))
        errors.concat(reportViolations("interventions",            PFX.INTERV,          badIntvs))
        errors.concat(reportViolations("prompt sets",              PFX.PROMPT_SET,      badPromptSets))
        errors.concat(reportViolations("prompts",                  PFX.PROMPT,          badPrompts))
        errors.concat(reportViolations("answers",                  PFX.ANSWER,          badAnswers))
    }
    return errors
}

const checkPrompts = () => {
    let errors = []
    const missingSelectionType = PROMPTS.filter((rec) => !("selectionType" in rec))
    const badSelectionType = PROMPTS.filter((rec) => ![ST.ONE, ST.OOM, ST.NONE].includes(rec.selectionType))

    if (!!missingSelectionType.length) {
        errors.push("missingSelectionType", missingSelectionType)
    }
    if (!!badSelectionType.length) {
        errors.push("badSelectionType", badSelectionType)
    }
    return errors
}

const checkAssessments = () => {
    let errors = []
    const missingTabLabels = ASSESSMENTS.filter((rec) => !("abbrLabel" in rec))
    if (!!missingTabLabels.length) {
        errors.push("Assessments missing tabLabels:", missingTabLabels)
    }
    return errors
}

const checkPromptsForMissingPromptSetIDs = () => {
    let errors = []
    const promptsMissingPromptSet = PROMPTS.filter((rec) => !("promptSetID" in rec))
    if (promptsMissingPromptSet.length) {
        errors.push("prompts withing promptSetID")
        errors.push(promptsMissingPromptSet)
    }
    return errors
}

const checkForInvalidPromptSetIDs = () => {
    let errors = []
    const promptsWithBadPromptSetIDs = PROMPTS
        .filter(rec => "promptSetID" in rec)
        .filter(rec => undefined === PROMPT_SETS.find(ps => ps.id === rec.promptSetID))
    if (promptsWithBadPromptSetIDs.length) {
        errors.push("prompts with bad promptsSetIDs")
        errors.push(promptsWithBadPromptSetIDs)
    }
    return errors
}


// looks like I got interupted in the middle of implementing this (UNUSED)
const checkPromptOptions = () => {
    INTV_META_DATA.filter((rec) => rec.type === TYPES.ANSWER)
}

const getNamingConventionViolations = (list, prefix) => {
    return list.filter(rec => !rec.id.startsWith(prefix))
}

const reportViolations = (label, properPrefix, violations) => {
    let errors = []
    if (violations.length > 0) {
        errors.push(`
The following ${label} don't begin with the prefix "${properPrefix}"
--------------------------------------------------------------------------------`)
        violations.forEach((rec) => errors.push(rec.id))
    }
    return errors
}

const assessments = CHECKLIST_META_DATA.filter(rec => [TYPES.ASSESS, TYPES.ASSESS_OPTION].includes(rec.type))
// console.log("assessments", assessments)
const checkScenarioForMissingAssessmentFindings = (scenario) => {
    // check for missing assessment findings in scenario data
    return assessments.filter(rec => !(rec.id in scenario.assessmentFindings))
}

const checkScenarioForInvalidAssessmentFindings = (scenario) => {
    // check for assessment findings in scenario data which aren't valid
    let invalids = []
    Object.keys(scenario.assessmentFindings).forEach((af) => {
        const found = assessments.find((rec) => rec.id === af)
        // console.log(af, found)
        if (found === undefined) {
            invalids.push(af)
        }
    })
    return invalids
}

const checkForMissingInterventionForms = () => {
    let errors = []
    INTVS.forEach((intv) => {
        // console.log(rec)
        if (!(intv.id in INTV_FORMS)) {
            errors.push(`missing from INTV_FORMS`)
            errors.push(JSON.stringify(intv, null, 4))
        }
    })
    return errors
}

// entry point for sanity checking routines
export const performSanityChecking = (scenarioData) => {
    let errors = []
    errors.concat(checkForDupIDs())
    errors.concat(enforceNamingConventions())
    errors.concat(checkPromptsForMissingPromptSetIDs())
    errors.concat(checkForInvalidPromptSetIDs())
    errors.concat(checkPrompts())
    errors.concat(checkAssessments())
    const missingFindings = checkScenarioForMissingAssessmentFindings(scenarioData)
    const invalidFindings = checkScenarioForInvalidAssessmentFindings(scenarioData)
    if (!!missingFindings.length) {
        errors.push("scenario missing assessmentFindings", missingFindings)
    }
    if (!!invalidFindings.length) {
        errors.push("scenario invalid assessment findings", invalidFindings)
    }
    errors.concat(checkForMissingInterventionForms())
    return errors
}


// merges generic scenario-type data into scenarioSpecificData
export const dataMerger = (scenarioSpecificData) => {
    let data = deepCopy(scenarioSpecificData)
    // mix in scenario-type stuff
    data.interventionForms = deepCopy(INTV_FORMS)
    data.interventionMenus = deepCopy(INTV_MENUS)
    data.phaseData = deepCopy(PHASE_DATA)
    data.systemInterventions = deepCopy(SYSTEM_INTVS)
    return data
}
