import { useState } from 'react';

export default function Medication({ med, index, medState }) {
    const [meds, setMeds] = medState;
    const [self, setSelf] = useState(med);

    function update(e) {
        const item = e.target;
        let updated = {...self};

        (item.type === "text")
            ? updated[item.name] = item.value
            : updated[item.name] = item.checked;

        setSelf(updated);

        meds[index] = updated;
        setMeds(meds.slice());
    }

    function deleteSelf() {
        meds.splice(index, 1);
        setMeds(meds.slice());
    }

    return (
        <div className="medication">
            <div className="medication-header">
                <button type="button" className="delete-bttn" onClick={deleteSelf}>-</button>
                <h3>{self.label ?? "New Medication"}</h3>
            </div>

            <fieldset>
                <label>
                    ID:
                    <input 
                        type="text" 
                        className="id" 
                        id={`${self.id}-id`} 
                        placeholder="ID" 
                        name="id"
                        onChange={update}
                        defaultValue={med.id}
                    />
                </label>

                <label>
                    Label:
                    <input 
                        type="text" 
                        className="label" 
                        id={`${self.id}-label`} 
                        placeholder="Label" 
                        name="label"
                        onChange={update}
                        defaultValue={med.label}
                    />
                </label>
            </fieldset>

            <fieldset>
                <label className="isPainMed">
                    Is Pain Medication?
                    <input 
                        type="checkbox" 
                        id={`${self.id}-isPainMed`} 
                        name="isPainMed" 
                        defaultChecked={med.isPainMed} 
                        onChange={update} />
                </label>

                <label className="isAirwayMed">
                    Is Airway Medication?
                    <input 
                        type="checkbox" 
                        id={`${self.id}-isAirwayMed`} 
                        name="isAirwayMed" 
                        defaultChecked={med.isAirwayMed} 
                        onChange={update} />
                </label>

                <label className="isFluid">
                    Is Fluid?
                    <input 
                        type="checkbox" 
                        id={`${self.id}-isFluid`} 
                        name="isFluid" 
                        defaultChecked={med.isFluid} 
                        onChange={update} />
                </label>
            </fieldset>
        </div>
    );
}