import { useEffect, useState } from "react"

import classnames from "classnames"

import { MDB_TYPES as TYPES } from "@meddbriefer/scenario-data/constants.js"

// NOTE: this data will eventually be coming from the db
import { CHECKLIST_META_DATA } from "@meddbriefer/scenario-data/traumaScenarioV2MetaData.js"

const getMetaData = () => {
    const recsWhichHaveOptions = CHECKLIST_META_DATA
                                    .filter(rec => [TYPES.AWO, TYPES.DECISION].includes(rec.type))
    return recsWhichHaveOptions.map(rec => {
        const options = CHECKLIST_META_DATA.filter(opt => opt.promptID === rec.id)
        rec.options = options
        return rec
    })

}


const CheckListCorrectness = ({active, scenario}) => {
    const [metaData, setMetaData] = useState([])
    const [correctAnswers, setCorrectAnswers] = useState({})

    useEffect(
        () => {
            setMetaData(getMetaData())
            setCorrectAnswers(scenario.checkListCorrectness)
        }, []
    )

    return (
        <div
            id="checklist-options"
            className={classnames("info-wrapper", {"scenario-view-hidden": !active})}
        >
            <div>Placeholder for Checklist step correctness</div>
            {/* {metaData.map(step =>
                <div key={step.id}>
                    <span>{step.label} <b>current correctValue:</b> {`${correctAnswers[step.id]}`}</span>
                    <br/>
                    {step.options.map(opt =>
                        <div key={opt.id}> &nbsp; &nbsp; {opt.label}</div>
                    )}
                </div>
            )} */}
        </div>
    )
}

export default CheckListCorrectness